<script>
    import { createEventDispatcher } from "svelte";
    export let multiple = true;
    let dragging = false;
    const dispatch = createEventDispatcher();

    export function getFilesFromDropEvent({ dataTransfer: { files, items } }) {
        return files.length
                ? [...files]
                : items
                        .filter(({ kind }) => kind === "file")
                        .map(({ getAsFile }) => getAsFile());
    }

    export function getFilesFromInputEvent({ target }) {
        const files = target.files ? [...target.files] : [];
        target.value = "";
        return files;
    }

    function startDragging() {
        dragging = true;
    }
    function stopDragging() {
        dragging = false;
    }
    const onFile = getFilesFunction => event => {
        stopDragging();
        const files = getFilesFunction(event);
        if (files.length) {
            dispatch("setfilename", { files: multiple ? files : files[0] });
        }
    };
</script>

<style>
    label {
        color: #5f5f5f;
        font-size: 0.85em;
        font-weight: 200;
    }
    input {
        position: absolute !important;
        height: 1px;
        width: 1px;
        overflow: hidden;
        clip: rect(1px 1px 1px 1px);
        clip: rect(1px, 1px, 1px, 1px);
        white-space: nowrap;
    }
    label:hover {
        cursor: pointer;
    }

    .dragarea {
        text-align: center;
        padding-top: 3vh;
        border: #ccc solid 1px;
        background-color: #ededed;
        width: 85%;
        max-width: 60vw;
        padding-right: 10vw;
        height: 3em;
    }
</style>

<main>
<label
        class:dragging
        on:drop|preventDefault={onFile(getFilesFromDropEvent)}
        on:dragover|preventDefault={startDragging}
        on:dragleave|preventDefault={stopDragging}>
    <slot {dragging}>
        <div class="dragarea">
            Drag &amp; Drop your file(s) or
            <strong>browse.</strong>
        </div>
    </slot>
    <input type="file" {multiple} on:input={onFile(getFilesFromInputEvent)} />
</label>
</main>
